.contacts {
  @include flex(space-between, top);
  max-width: 1230px;
  margin-left: auto;
  margin-right: auto;
  @include xs {
    display: block; } }

.contact {
  width: 33.33333%;
  text-align: center;
  color: $white;
  max-width: 270px;
  padding-left: 10px;
  padding-right: 10px;
  @include xs {
    width: auto;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px; }
  &__icon {
    margin-bottom: 15px;
    height: 90px;
    width: 90px;
    border: 12px solid  $gray-dark;
    border-radius: 50%;
    @include flex(center, center);
    margin-left: auto;
    margin-right: auto;
    .icon {
      fill: currentColor;
      font-size: 32px; } }
  &__text {
    text-align: center;
    a {
      @include hover {
        text-decoration: underline; } } } }

.contacts-form {
  @include flex(flex-end, normal, row, wrap);
  &__left {
    width: percentage(7/12);
    position: relative;
    @include flex(center, normal, column);
    @include md {
      width: 100%;
      margin-bottom: 15px;
      padding-left: 0; } }
  &__right {
    width: percentage(5/12);
    @include flex(center, normal, column);
    @include md {
      width: 100%; } }
  &__title {
    margin-bottom: 25px;
    max-width: 590px;
    @include md {
      max-width: none;
      margin-bottom: 15px;
      text-align: center; } }
  &__form-title {
    margin-bottom: 10px;
    .h4 {
      margin-bottom: 10px; } }
  &__text {
    max-width: 525px;
    position: relative;
    @include md {
      max-width: none;
      text-align: center;
      width: auto; } } }

.map {
  position: relative;
  padding-top: 50%;
  background-color: $gray;
  iframe {
    @include coverdiv;
    z-index: 1; } }
