.news {
  &__tabs {
    margin-bottom: 20px; }
  &__tab {
    display: inline-block;
    padding: 15px 0;
    border-bottom: 4px solid transparent;
    background-color: transparent;
    color: $gray;
    font-size: inherit;
    @include tr(color .3s, border-color .3s);
    @include notlast {
      margin-right: 60px;
      @include lg {
        margin-right: 30px; }
      @include xs {
        margin-right: 5px; } }
    @include lg {
      padding: 7px 0; }
    &.is-active {
      color: $red;
      @include semi;
      border-color: $red; }
    @include hover {
      @extend .news__tab.is-active; } }
  &__items {
    @include flex(flex-start, normal, row, wrap);
    @include notlast {
      margin-bottom: calc(var(--main-padding-md, 45px)/2); } }

  &__item {
    width: 33.33333%;
    border-bottom: 1px solid  $gray-lightest;
    border-right: 1px solid  $gray-lightest;
    @include sm {
      width: 50%; }
    @include xs {
      width: 100%; } }
  &__btn {
    text-align: center; } }


.news-item {
  $this: &;
  position: relative;
  @include flex(flex-start, normal, column);
  height: 100%;
  @include hover {
    #{$this}__cover {
      visibility: visible;
      opacity: 1; } }
  &__content {
    background-color: $white;
    flex-grow: 1;
    padding: 30px;
    @include flex(flex-start, normal, column);
    @include lg {
      padding: 20px; }
    @include md {
      padding: 15px; } }
  &__img {
    padding-top: 50%;
    display: block;
    position: relative;
    overflow: hidden;
    @include lg {
      padding-top: 60%; } }
  &__img-img {
    @include cover;
    @include coverdiv;
    display: block; }
  &__upttl {
    text-transform: uppercase;
    font-size: 14px;
    display: block;
    @include semi;
    color: $gray;
    margin-bottom: 20px;
    @include md {
      margin-bottom: 10px; } }
  &__title {
    margin-bottom: 15px;
    display: block;
    @include md {
      margin-bottom: 5px; } }
  &__text {
    flex-grow: 1;
    display: block;
    margin-bottom: 15px;
    @include md {
      margin-bottom: 5px; } }
  &__date {
    color: $gray;
    @include before {
      display: inline-block;
      border-top: 1px solid  currentColor;
      width: 35px;
      margin-right: 5px;
      position: relative;
      top: -.25em; } }
  &__cover {
    @include coverdiv;
    background-color: rgba($black, .9);
    @include flex(center, center);
    visibility: hidden;
    opacity: 0;
    @include tr(visibility .3s, opacity .3s);
    @include before {
      @include icon-arrow(30, 30, 3, $white, 225);
      display: block; } } }



.news-article-sm {
  $this: &;
  text-align: left;
  display: block;
  @include hover {
    #{$this}__date {
      @include before {
        border-color: $red; } } }
  &__date {
    position: relative;
    display: block;
    @include before {
      display: inline-block;
      margin-right: 10px;
      border-top: 1px solid currentColor;
      width: 40px;
      position: relative;
      top: -.3em;
      @include tr(border-color .3s); } }
  &__title {
    margin-bottom: 15px;
    display: block;
    position: relative;
    @include md {
      margin-bottom: 10px; } }
  &__text {
    display: block; } }
