.history {
  $this: &;
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;
  &__left {
    width: percentage(7/12);
    padding-left: 15px;
    padding-right: 15px;
    padding-top: var(--section-padding-lg, 100px);
    @include md {
      width: 100%;
      padding-top: 50px; } }
  &__right {
    width: percentage(5/12);
    padding-left: 15px;
    padding-right: 15px;
    @include md {
      width: 100%; } }
  &__block {
    opacity: 0; }

  &--center {
    .history-table {
      &__row {
        height: 50%; } }
    #{$this}__left {
      padding-top: 0;
      @include flex(center, normal, column);
      min-height: 90vh;
      @include md {
        min-height: 0;
        padding-top: 50px;
        padding-bottom: 50px; } } } }

.history-block {
  $this: &;
  @include flex();
  position: relative;
  color: $white;
  &__title {
    text-align: center;
    position: absolute;
    z-index: 1;
    top: -2em;
    left: 0;
    right: 0; }
  &__img {
    flex: 0 0 50%;
    margin-left: auto;
    margin-left: -15%;
    position: relative;
    z-index: 0;
    @include before {
      @include image('about/texture-02.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position: 50% 50%;
      width: 180%;
      height: 150px;
      top: -30px;
      right: -15px;
      z-index: -1;
      position: absolute; }
    @include lg {
      margin-left: -50%; } }
  &__img-img {
    @include cover;
    padding-top: 120%;
    position: relative;
    z-index: 1px; }
  &__content {
    flex: 0 0 65%;
    position: relative;
    z-index: 1;
    @include flex(center, normal, column);
    @include lg {
      flex: 0 0 100%; } }
  &__content-title {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__list-item {
    font-size: 24px;
    margin-bottom: 15px;
    position: relative;
    @include md {
      margin-bottom: 10px; }
    @include sm {
      font-size: 16px; }
    @include before {
      border-top: 2px solid  $red;
      position: absolute;
      top: 1.3em;
      left: 0;
      width: 25px; } }
  &__list-item-sm {
    line-height: 2;
    @include notlast {
      margin-bottom: 5px; } }

  &--blue {
    color:#59649d {} }
  &--simple {
    #{$this}__content {
      flex: 0 0 100%; }
    #{$this}__img {
      flex: 0 0 100%;
      margin-left: 0; }
    #{$this}__img-img {
      padding-top: 50%; } }
  &--img-diff {
    #{$this}__img {
      @include before {
        @include image('about/texture-03.png');
        height: 70%;
        width: 50%;
        background-position: 0 0;
        top: 30px;
        right: auto;
        left: -30px;
        bottom: auto; } } } }

.history-table {
  --bd-color: #18214f;
  border-left: 1px solid  var(--bd-color);
  height: 100%;
  &__date {
    @include semi;
    font-size: 36px;
    text-transform: uppercase;
    color: #59649d;
    @include flex(center, normal, column);
    min-height: 190px;
    padding-left: 20px;
    padding-right: 20px;
    width: percentage(145/570);
    position: relative;
    text-align: center;
    @include after {
      position: absolute;
      top: 0;
      right: 0;
      height: 70%;
      border-right: 1px solid var(--bd-color); }
    @include lg {
      font-size: 28px; }
    @include md {
      font-size: 24px;
      padding-left: 10px;
      padding-right: 10px;
      min-height: 140px; }
    @include sm {
      font-size: 18px; } }
  &__text {
    color: #c1c9f3;
    @include flex(center, normal, column);
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    width: percentage(425/570);
    @include md {
      padding-left: 10px;
      padding-right: 10px; } }
  &__row {
    position: relative;
    transform: translateX(100vw);
    @include flex;
    @include notlast {
      @include before {
        border-top: 1px solid  var(--bd-color);
        width: 50vw;
        position: absolute;
        bottom: 0;
        left: 0;
        @include md {
          width: 100%; } } } }

  &--gray {
    --bd-color: #{$gray}; } }

