.hero {
  @include cover;
  min-height: 50vh;
  padding-top: $header-height;
  @include flex(center, center);
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  opacity: 0;
  @include before {
    // +coverdiv
    // background-color: rgba($black, .4)
 }    // z-index: 0
  @include xl {
    padding-top: $header-height-md; }
  @include lg {
    padding-top: $header-height-mob; }
  &__block {
    text-align: center;
    position: relative;
    z-index: 1;
    margin-top: 90px;
    margin-bottom: 90px;
    width: 100%; }
  &__title {
    margin-bottom: 10px;
    opacity: 0; }
  &__subttl {
    max-width: 930px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 10px;
    overflow: hidden;
    .h2-subttl {
      transform: translateY(-130%); } }
  &__breadcrumbs {
    opacity: 0; }
  &__breadcrumbs-top {
    position: absolute;
    top: calc(#{$header-height} + #{$subnav-height} + 10px);
    left: var(--section-padding, 40px);
    z-index: 1;
    @include xl {
      top: calc(#{$header-height-md} + #{$subnav-height} + 10px); }
    @include lg {
      top: calc(#{$header-height-mob} + 10px); } }
  &__slider {
    width: 100%;
    text-align: left;
    opacity: 0; }
  &__scroll {
    @extend .hero-about__scroll;
    @include sm {
      position: absolute !important; } }
  &__date {
    color: $gray-light;
    margin-bottom: 15px;
    transform: translateY(-100%);
    .icon {
      fill: currentColor;
      position: relative;
      top: .1em;
      right: 5px; } }

  &--lg {
    min-height: 75vh;
    @include sm {
      min-height: 50vh; } }
  &--light {
    @include before {
      display: none; } }
  &--full {
    min-height: 100vh; } }

.hero-about {
  // min-height: 100vh
  color: $white;
  display: block;
  padding-top: 0 !important;
  overflow: hidden;
  padding-left: 0;
  padding-right: 0;
  &__inner {
    @include flex(flex-start, normal, row, wrap);
    height: 100vh;
    padding-top: $header-height;
    @include xl {
      padding-top: $header-height-md; }
    @include lg {
      padding-top: $header-height-mob; }
    @include sm {
      height: auto;
      padding-bottom: 30px;
      padding-top: $header-height-mob + 30px; } }
  &__left {
    @include flex(center, normal, column);
    width: percentage(7/12);
    border-right: 1px solid  $gray;
    padding-right: 10px;
    transform: translateY(-100%);
    @include sm {
      width: 100%;
      border-right: 0;
      margin-bottom: 15px; } }
  &__right {
    width: percentage(5/12);
    @include sm {
      width: 100%; } }
  &__top, &__bottom {
    @include flex(center, normal, column);
    height: 50%;
    padding-left: percentage(1/5);
    @include lg {
      padding-left: 30px; }
    @include md {
      padding-left: 15px; }
    @include sm {
      padding-left: 0; } }
  &__top {
    position: relative;
    transform: translateX(100vw);
    @include before {
      border-top: 1px solid  $gray;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 50vw; }
    @include sm {
      margin-bottom: 15px;
      @include before {
        display: none; } } }
  &__scroll {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    padding-bottom: 30px;
    @include sm {
      position: static; } } }

.hero-about-block {
  position: relative;
  opacity: 0;
  &__title-sm {
    text-transform: uppercase;
    margin-bottom: 10px; }
  &__nmb {
    position: absolute;
    @include css-lock(20, 408, 320, 1199, font-size);
    opacity: .05;
    @include bold;
    line-height: .7;
    top: 0;
    left: 0;
    transform: translate(-30%, -30%); } }


